o que cumplir, un destino al cual llegar en la vida, no tiene por qué ser tácito, incluso puede que el personaje no tenga claro cuál es su objetivo, pero debe existir para que tenga la motivación de superar los obstáculos.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-4 mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img-a",attrs:{"src":require("@/assets/curso/tema_3/img_4.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-lg-6 mb-4 mb-lg-0"},[_c('h4',[_vm._v("Estereotipo")]),_c('div',{staticClass:"line01 mb-3"}),_c('p',[_vm._v("Evitar los estereotipos a menos que sea necesario es una buen práctica, así como un personaje sin motivación puede ser aburrido, un personaje estereotipado puede ser predecible y por tanto, poco interesante.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-4 mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img-a",attrs:{"src":require("@/assets/curso/tema_3/img_5.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-lg-6 mb-4 mb-lg-0"},[_c('h4',[_vm._v("Empezar de nuevo está bien")]),_c('div',{staticClass:"line01 mb-3"}),_c('p',[_vm._v("Si el personaje no funciona no tiene nada de malo desecharlo y empezar nuevamente, por los menos eso permite saber qué cosa no debe ser el personaje.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-4 mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img-a",attrs:{"src":require("@/assets/curso/tema_3/img_6.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-lg-6 mb-4 mb-lg-0"},[_c('h4',[_vm._v("Arquetipos")]),_c('div',{staticClass:"line01 mb-3"}),_c('p',[_vm._v("A lo largo de su vida el psicoterapeuta suizo Carl Gustav Jung desarrolló lo que actualmente es conocido como los arquetipos de Jung, en principio los generó para establecer una teoría so